import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-bai2',
  templateUrl: './bai2.component.html',
  styleUrls: ['./bai2.component.css']
})
export class Bai2Component implements OnInit {
  diem = {
    c: null,
    hl: null,
  }
  constructor() { }

  ngOnInit() {
  }
  grade() {
    if (this.diem.c < 5) {
      this.diem.hl = 'chúc mừng bạn đã rớt';
    } else {
      if (this.diem.c >= 5) {
        this.diem.hl = " chúc mừng bạn đã đậu";
      }
    }

  }
}
